.photo-grid { 
  display: grid;
  grid-template-columns: repeat(4,1fr);
  margin: 5vh auto;
  width: 90vh;
  grid-gap: 20px;
  -ms-flex-align: center;
      align-items: center;
  justify-items: center;
}

.photo-grid img {
  border: 1px solid #ccc;
  -webkit-box-shadow: 2px 2px 6px 0px  rgba(0,0,0,0.3);
          box-shadow: 2px 2px 6px 0px  rgba(0,0,0,0.3);
  max-width: 100%;
}

.main-photo-container {
  position: relative;
  display: none;
}

/* Expanding image text */
.imgtext {
  color: whitesmoke;
  font-size: 20px;
}

/* Closable button inside the image */
.closebtn {
  position: absolute;
  top: 10px;
  right: 15px;
  color: white;
  font-size: 35px;
  cursor: pointer;
}

.subnav {
  margin-top: -20px;
}

/* Buttons */
div .prev,
.next {
  border: none;
  -webkit-box-shadow: none;
          box-shadow: none;
  cursor: pointer;
  position: absolute;
  top: 50%;
  width: auto;
  padding: 16px;
  margin-top: -50px;
  color: white;
  font-weight: bold;
  font-size: 20px;
  -webkit-transition: 0.6s ease;
  -o-transition: 0.6s ease;
  transition: 0.6s ease;
  -moz-user-select: none;
   -ms-user-select: none;
       user-select: none;
  -webkit-user-select: none;
  background-color: rgba(0,0,0,0);
  background-image: none;
}

div .prev:focus, .next:focus{
  outline: none;
  background-color: rgba(0,0,0,0);
  color: white;
}

/* Position the buttons */
.next {
  right: 0;
  border-radius: 0 3px 3px 0;
}

.prev{
  left: 0;
  border-radius: 3px 0 0 3px;
}

/* On hover, add a black background color with a little bit see-through */
.prev:hover,
.next:hover {
  background-color: rgba(0, 0, 0, 0.8);
}

.transparent{
  background: rgba(0,0,0,0);
  -webkit-box-shadow: none;
          box-shadow: none;
  border: none;
}

.modal-backdrop.dark{
  opacity: .9;
}

.transparent.modal-dialog {
  width: 60vw;
}

.transparent.modal-dialog .modal-content{
  background: rgba(0,0,0,0);
  width: 60vw;
  -webkit-box-shadow: none;
          box-shadow: none;
  border: none;
}


